import React from 'react';
import PropTypes from 'prop-types';
import './index.css';
import Helmet from "react-helmet";
import styles from './global.module.scss';
import Header from '../components/header';



export default  class TemplateWrapper extends React.Component {
	render() {
    const { children } = this.props;
    	return <div> 
    				  	<Helmet>
    					  <title>Genijalac</title>
    					  <meta name="description" content="Materijali za nastavu matematike" />

                          
    					  <link href="https://fonts.googleapis.com/css?family=Work+Sans:400,700" rel="stylesheet"></link>
                          <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/KaTeX/0.8.3/katex.min.css" integrity="sha384-B41nY7vEWuDrE9Mr+J2nBL0Liu+nl/rBXTdpQal730oTHdlrlXHzYMOhDU60cwde" crossorigin="anonymous"></link>
<script src="https://cdnjs.cloudflare.com/ajax/libs/KaTeX/0.8.3/katex.min.js" integrity="sha384-L9gv4ooDLrYwW0QCM6zY3EKSSPrsuUncpx26+erN0pJX4wv1B1FzVW1SvpcJPx/8" crossorigin="anonymous"></script>
    				  	</Helmet>
    			 		<Header></Header>
    			    	{children()}
    </div>
    	
			
}
}


